import ExcelJS from 'exceljs';

export interface AttendanceData {
  employeeName: string;
  employeeId: string;
  department: string;
  startDate: string;
  endDate: string;
  records: AttendanceRecord[];
}

export interface AttendanceRecord {
  date: string;
  dayOfWeek: string;
  employeeName: string;
  employeeId: string;
  department: string;
  position: string;
  workType: string;
  clockIn?: string;
  clockOut?: string;
  status: string;
  remarks?: string;
}

export async function generateAttendanceExcel(data: AttendanceData): Promise<Blob> {
  const workbook = new ExcelJS.Workbook();
  const worksheet = workbook.addWorksheet('');

  //   
  worksheet.pageSetup = {
    paperSize: 9, // A4
    orientation: 'portrait',
    margins: {
      left: 0.7,
      right: 0.7,
      top: 0.75,
      bottom: 0.75,
      header: 0.3,
      footer: 0.3,
    },
  };

  //    (   )
  worksheet.columns = [
    { width: 12 }, // 
    { width: 8 },  // 
    { width: 12 }, // 
    { width: 8 },  // 
    { width: 6 },  // 
    { width: 8 },  // 
    { width: 12 }, // 
    { width: 12 }, // 
    { width: 8 },  // 
    { width: 8 },  // 
  ];

  //   (1 )
  const titleRow = worksheet.addRow(['1']);
  titleRow.height = 25;
  worksheet.mergeCells('A1:J1');
  const titleCell = worksheet.getCell('A1');
  titleCell.value = '1';
  titleCell.font = { name: ' ', size: 14, bold: true };
  titleCell.alignment = { horizontal: 'center', vertical: 'middle' };

  //  
  worksheet.addRow([]);

  //   
  const employeeRow = worksheet.addRow([`${data.employeeName}  (21)`]);
  employeeRow.height = 20;
  worksheet.mergeCells('A3:J3');
  const employeeCell = worksheet.getCell('A3');
  employeeCell.font = { name: ' ', size: 12, bold: true };
  employeeCell.alignment = { horizontal: 'left', vertical: 'middle' };

  const periodRow = worksheet.addRow([`: ${data.startDate} ~ ${data.endDate}`]);
  periodRow.height = 20;
  worksheet.mergeCells('A4:J4');
  const periodCell = worksheet.getCell('A4');
  periodCell.font = { name: ' ', size: 10 };
  periodCell.alignment = { horizontal: 'left', vertical: 'middle' };

  //  
  const headerRow = worksheet.addRow([
    '',
    '', 
    '',
    '',
    '',
    '',
    '',
    '',
    '',
    ''
  ]);
  
  headerRow.height = 25;
  
  //  
  headerRow.eachCell((cell, _colNumber) => {
    cell.font = { name: ' ', size: 10, bold: true };
    cell.alignment = { horizontal: 'center', vertical: 'middle' };
    cell.border = {
      top: { style: 'thin' },
      left: { style: 'thin' },
      bottom: { style: 'thin' },
      right: { style: 'thin' }
    };
    cell.fill = {
      type: 'pattern',
      pattern: 'solid',
      fgColor: { argb: 'FFE0E0E0' }
    };
  });

  // /  
  const isWeekendOrHolidayFunc = (dateString: string): boolean => {
    const date = new Date(dateString.split('(')[0]); //   
    const dayOfWeek = date.getDay();
    
    //  (: 6, : 0)
    if (dayOfWeek === 0 || dayOfWeek === 6) {
      return true;
    }
    
    //  (   -  )
    const year = date.getFullYear();
    const month = date.getMonth() + 1;
    const day = date.getDate();
    
    //   (  )
    const holidays = [
      `${year}-01-01`, // 
      `${year}-03-01`, // 
      `${year}-05-05`, // 
      `${year}-06-06`, // 
      `${year}-08-15`, // 
      `${year}-10-03`, // 
      `${year}-10-09`, // 
      `${year}-12-25`, // 
    ];
    
    const dateStr = `${year}-${month.toString().padStart(2, '0')}-${day.toString().padStart(2, '0')}`;
    return holidays.includes(dateStr);
  };

  //   
  data.records.forEach(record => {
    // /    
    let workStatus = record.remarks || '-';
    const isWeekendOrHoliday = isWeekendOrHolidayFunc(record.date);
    
    //     
    if (record.remarks && record.remarks.includes('')) {
      workStatus = '';
    } else if (isWeekendOrHoliday) {
      const date = new Date(record.date.split('(')[0]);
      const dayOfWeek = date.getDay();
      
      if (dayOfWeek === 0 || dayOfWeek === 6) {
        workStatus = '';
      } else {
        workStatus = '';
      }
    }

    const dataRow = worksheet.addRow([
      record.date,
      record.employeeName,
      record.employeeId,
      record.department,
      record.position,
      record.workType,
      record.clockIn || '-',
      record.clockOut || '-',
      record.status,
      workStatus
    ]);

    dataRow.height = 20;
    
    const isSpecialDay = isWeekendOrHoliday;
    const isLeaveDay = record.remarks && record.remarks.includes('');
    
    //   
    dataRow.eachCell((cell, colNumber) => {
      cell.font = { name: ' ', size: 9 };
      cell.alignment = { 
        horizontal: colNumber === 1 ? 'center' : 'left', //  ,  
        vertical: 'middle' 
      };
      cell.border = {
        top: { style: 'thin' },
        left: { style: 'thin' },
        bottom: { style: 'thin' },
        right: { style: 'thin' }
      };
      
      //    
      if (colNumber === 7 || colNumber === 8) {
        cell.alignment = { horizontal: 'center', vertical: 'middle' };
      }
      
      //  :  > / >  
      if (isLeaveDay) {
        //   
        cell.fill = {
          type: 'pattern',
          pattern: 'solid',
          fgColor: { argb: 'FFE8F5E8' } // 
        };
      } else if (isSpecialDay) {
        // /  
        cell.fill = {
          type: 'pattern',
          pattern: 'solid',
          fgColor: { argb: 'FFFFE8E8' } // 
        };
      }
    });
  });

  //   Blob 
  const buffer = await workbook.xlsx.writeBuffer();
  return new Blob([buffer], { 
    type: 'application/vnd.openxmlformats-officedocument.spreadsheetml.sheet' 
  });
}

//   
export function getDayOfWeek(dateString: string): string {
  const date = new Date(dateString);
  const days = ['', '', '', '', '', '', ''];
  return days[date.getDay()];
}

//    (YYYY-MM-DD YYYY-MM-DD() )
export function formatDateWithDay(dateString: string): string {
  const dayOfWeek = getDayOfWeek(dateString);
  return `${dateString}(${dayOfWeek})`;
}